"use client";

import Link from "next/link";
import { Button } from "@/components/ui/button";

export default function Home() {
  return (
    <main className="min-h-screen flex flex-col items-center justify-center bg-gradient-to-br from-slate-50 via-white to-slate-100 px-4 sm:px-6 lg:px-8">
      <div className="w-full max-w-4xl mx-auto text-center">
        <h1 className="text-4xl sm:text-5xl font-bold mb-6 text-center px-4">
          Welcome to Optifi
        </h1>
        <p className="mb-8 text-lg sm:text-xl text-slate-600 max-w-2xl mx-auto text-center px-4 leading-relaxed">
          Smart financial tracking through OCR and AI-driven insights. Visualize
          your spending, scan receipts, and forecast your budget with ease.
        </p>
        <div className="flex flex-col sm:flex-row gap-4 justify-center px-4">
          <Link href="/register" className="w-full sm:w-auto">
            <Button size="lg" className="text-lg px-8 py-6 w-full sm:w-auto">
              Get Started
            </Button>
          </Link>
          <Link href="/login" className="w-full sm:w-auto">
            <Button
              size="lg"
              variant="outline"
              className="text-lg px-8 py-6 w-full sm:w-auto"
            >
              Login
            </Button>
          </Link>
        </div>
      </div>
    </main>
  );
}
